.columns {
  background-color: $color-organizations-bg;
  width: 100%;
  padding: 46px 0 86px 0;
  background: url('../../assets/3 Columns Background.png');

  .wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.three-box {
  max-width: 334px;
  text-align: center;
  margin: 0 40px;

  .three-number {
    font-size: 90px;
    font-family: $font-alt;
    font-weight: 300;
    padding-bottom: 10px;
    line-height: 122px;
    color: $color-organizations-text;
  }

}

.title {
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: -0.36px;
  line-height: 30px;
}

.text {
  font-size: 16px;
  font-weight: 300;
  letter-spacing: -0.32px;
  line-height: 190%;
}
